<template lang="pug">

  #app
    Part1.part1
    Part2.part2
    Part3.part3

</template>

<script>

  import Part1 from './components/Part1.vue';
  import Part2 from './components/Part2.vue';
  import Part3 from './components/Part3.vue';

  export default {
    name: "app",
    components: {
      Part1,
      Part2,
      Part3
    },
    created: () => {
      let app = document.getElementById('app');
      app.style.paddingRight = app.offsetWidth - app.clientWidth + 'px';
    }
  };

</script>

<style lang="sass">

  @import 'vars'

  *
    font-family: $sans-serif
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    box-sizing: border-box


  body
    position: absolute
    top: 0
    left: 0
    bottom: 0
    right: 0
    margin: 0

    #app
      background-image: url("assets/wallpaper_bright.jpg")
      background-size: cover
      display: grid
      grid-template-areas: 'part1 part2' 'part1 part3'
      grid-template-columns: 1.2fr 1.8fr
      height: 100%
      width: 100%

      .part1
        grid-area: part1
        box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.2)

      .part2
        grid-area: part2
